h1 {
  color: #4b484b;
  text-align: center;
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

.form {
  flex-direction: column;
  max-width: 600px;
  display: flex;
}

.box {
  justify-content: center;
  align-items: center;
  display: flex;
}

.label {
  margin-bottom: 7px;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 20px;
}

.input {
  color: #031649;
  background-color: #eee;
  border: 1px solid #ccc;
  border-radius: 10px;
  outline: none;
  max-width: 500px;
  height: 35px;
  margin-bottom: 20px;
  padding-left: 10px;
  font-size: 20px;
  transition: border .3s;
}

.input:focus {
  background-color: #d1d3d3;
  border: 1px solid #ececec;
}

.btn-box {
  justify-content: center;
  gap: 10px;
  display: flex;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  width: 140px;
  height: 40px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 600;
  transition: background-color .3s;
}

.save {
  color: #fff;
  background-color: #4caf50;
}

.save:hover {
  background-color: #19d122;
}

.del {
  color: #fff;
  background-color: #e6574d;
}

.del:hover {
  background-color: #ff0400;
}

.msg-save, .msg-del {
  color: #000;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
